<template>
  <div class="dual-banner critical-component-clear">
    <div
      class="dual-banner__container"
      :class="bannerAlignment"
      js-dual-banner="item"
      v-html="bannerItems"
    />
  </div>
</template>

<script>
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/all'
import transformations from '~/helpers/transformations'

/**
 * Vue: Dual banner (dual-banner)
 * -----------------------------------------------------------------------------
 * Section: Dual Banner.
 *
 * @param {String} bannerItems - Section's banner items.
 * @param {String} bannerAlignment - Section's banner alignment.
 *
 */
export default {
  name: 'DualBanner',

  props: {
    bannerItems: {
      type: String,
      default: '',
    },
    bannerAlignment: {
      type: String,
      default: '',
    },
  },

  data() {
    return {
      selectors: {
        banner: '[js-dual-banner="item"]',
        block: '[js-dual-banner="block"]',
        media: '[js-dual-banner="media"]',
      },
    }
  },

  mounted() {
    gsap.registerPlugin(ScrollTrigger)
    this.addAnimation()
  },

  methods: {

    /**
     * Add GSAP animation to section
     */
    addAnimation() {
      const blocks = this.$el.querySelectorAll(this.selectors.block)
      const container = this.$el.querySelectorAll(this.selectors.banner)

      if (!blocks) {
        return
      }

      blocks.forEach((banner, i) => {
        const bannerMedia = banner.querySelectorAll(this.selectors.media)
        const tl = gsap.timeline({
          scrollTrigger: {
            trigger: container,
            start: 'top bottom',
            end: 'center center',
            scrub: 0.2,
          },
        })

        tl.fromTo(banner,
          {
            delay: 0,
            y: transformations.dualParallax[i],
          },
          {
            delay: 0,
            y: 0,
            ease: 'power1.easeInOut',
          }, 0)

        tl.fromTo(bannerMedia,
          {
            scale: 1.25,
          },
          {
            scale: 1,
            ease: 'power1.easeInOut',
          }, 0)
      })
    },
  },
}
</script>

<style lang="scss">
@import './dual-banner';
</style>
